/* */ 

interface ServiceInit {
  status: 'init';
}
interface ServiceLoading {
  status: 'loading';
}
interface ServiceLoaded<T> {
  status: 'loaded';
  payload: T;
}
interface ServiceError {
  status: 'error';
  error: Error;
}

export type Service<T> =
  | ServiceInit
  | ServiceLoading
  | ServiceLoaded<T>
  | ServiceError;

interface IYoutube {
  author_name: string
  html: string
  version: string
  author_url: string
  thumbnail_width: number
  provider_url: string
  type: string
  title: string
  height: number
  thumbnail_height: number
  thumbnail_url: string
  provider_name: string
  width: number
}

const useYoutubeData = (id: string) => {
  const [result, setResult] = useState<Service<IYoutube>>({
    status: 'loading'
  });

  useEffect(() => {
    if (id) {
      setResult({ status: 'loading' });
      fetch(`https://cors-anywhere.herokuapp.com/https://www.youtube.com/oembed?url=https://www.youtube.com/watch?v=${ id }&format=json`)
        .then(response => response.json())
        .then(response => setResult({ status: 'loaded', payload: response }))
        .catch(error => setResult({ status: 'error', error }));
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [id]);

  return result;
};

